const BUY_STATES = {
    'not_started': 'Instant Exchange',
    'initializing': 'Initializing...',
    'allowance': 'Checking Allowance',
    'approving': 'Creating Approve Transaction',
    'validation': 'Validating...',
    'create_tx': 'Creating Transaction',
    'send_tx': 'Sending Transaction',
    'submitted': "Order Submitted",
    'pending': 'Pending...',
    'failed': "Transaction failed",
}

class InstantSwap extends React.Component {
    static contextType = ThemeContext

    constructor(props) {
        super(props);
        this.web3 = new Web3(Web3.givenProvider || process.env.REACT_APP_WSS_URL)
        this.api = InstantSwapApi;
        this.typeTimeout = undefined;
        this.isExchangeInProgress = false;

        this.state = {
            loading: false,
            loadingState: {
                all: 32,
                loaded: 0,
            },
            tokens: [],
            pair: {
                deposit: {
                    token: null,
                    value: '',
                },
                destination: {
                    token: null,
                    value: '',
                },
            },
            hasEnoughBalance: true,
            rates: [],
            rate: undefined,
            showMore: false,
            buyState: 'not_started',
            max: 0,
            showQrModal: false,
            coinSwitchOrder: {},
            simpleSwapOrder: {}
        }
    }


    onChangeBalance = (balance) => {
        this.setState({
            max: balance,
        })
    }

    promiseHandler = (callback) => {
        return new Promise((resolve) => {
            callback()
                .then(response => resolve(response))
                .catch(error => resolve(undefined));
        })
    }

    getParaswapSortedRates = rates => {
        return _.orderBy(rates.others, ['rate'], ['desc'])
    }

    getDexagSortedRates = rates => {
        return _.orderBy(rates, ['price'], ['desc'])
    }

    getSortedRates = (response, type) => {
        if(!response) return [];
        switch(type) {
            case 'paraswap': {
                return this.getParaswapSortedRates(response);
            }
            case 'dexag': {
                return this.getDexagSortedRates(response);
            }
            default: {
                return response.hasOwnProperty('data') ? response.data : response;
            }
        }
    }

    transformRates = (rates) => {
        let result = [];
        for(let i in rates) {
            const key = rates[i][0];
            const apiRates = rates[i][1];
            switch(key) {
                case '1inch': {
                    if(apiRates && typeof apiRates.toTokenAmount === 'string') {
                        result.push({
                            rate: apiRates.toTokenAmount / (10 ** this.state.pair.destination.token.decimals),
                            platform: 'oneInch',
                            source: '1inch',
                        })
                    }
                    break;
                }
                case 'paraswap': {
                    if(apiRates) {

                        apiRates.forEach(rate => {
                            if(supportedDEXes['paraswap'].includes(rate.exchange)) {
                                result.push({
                                    rate: rate.unit / (10 ** this.state.pair.destination.token.decimals),
                                    platform: rate.exchange,
                                    source: 'paraswap',
                                })
                            }
                        })
                    }
                    break;
                }
                case 'dexag': {
                    if(apiRates) {

                        apiRates.forEach(rate => {
                            if(supportedDEXes['dexag'].includes(rate.dex)) {
                                result.push({
                                    rate: Number(rate.price),
                                    platform: rate.dex,
                                    source: 'dexag',
                                })
                            }
                        })
                    }
                    break;
                }
                case 'coinswitch': {
                    if(apiRates.hasOwnProperty('data') && apiRates.data !== null) {
                        const data = apiRates.data;
                        result.push({
                            rate: data?.rate,
                            min: data?.limitMinDepositCoin,
                            max: data?.limitMaxDepositCoin,
                            platform: 'coinSwitch',
                            source: 'coinswitch',
                        })
                    }
                    break;
                }
                case 'simpleSwap': {
                    if(apiRates.rate !== null) {
                        result.push({
                            rate: Number(apiRates.rate),
                            min: Number(apiRates.min || 0),
                            max: Number(apiRates.max || 0),
                            platform: 'simpleSwap',
                            source: 'simpleSwap',
                        })
                    }
                }
            }
        }

        return result;
    }

    getSortedResult = (response) => {
        let sortedParts = Object.keys(response).map(key => [key, this.getSortedRates(response[key], key)]);
        let transformedRates = this.transformRates(sortedParts);
        return _.sortBy(transformedRates, (o) => -o.rate);
    }

    fetchPrices = async (pair) => {
        let { deposit, destination } = pair;

        if(deposit.token !== null && destination.token !== null) {
            this.setState({
                loading: true,
                loadingState: {
                    all: 32,
                    loaded: 0,
                }
            })
            let fromAmount = (10 ** deposit.token.decimals);

            let dexagParams = {
                to: destination.token.symbol,
                from: deposit.token.symbol,
                dex: 'all',
                fromAmount: 1
            }

            let oneInch = await this.promiseHandler(() => this.api.oneInch.get('quote', {
                fromTokenAddress: deposit.token.address,
                toTokenAddress: destination.token.address,
                amount: fromAmount,
            }))

            this.setState({
                loadingState: {
                    all: 32,
                    loaded: 10,
                }
            })

            let coinSwitch = await this.promiseHandler(() => this.api.coinSwitch.get('rate', {
                depositCoin: deposit.token.symbol.toLowerCase(),
                destinationCoin: destination.token.symbol.toLowerCase(),
            }))

            this.setState({
                loadingState: {
                    all: 32,
                    loaded: 15
                }
            })

            let paraswap = await this.promiseHandler(() => this.api.paraswap.getRate(deposit.token.address, destination.token.address, fromAmount));

            this.setState({
                loadingState: {
                    all: 32,
                    loaded: 20,
                }
            })

            let dexag = await this.promiseHandler(() => this.api.dexag.sdk.getPrice(dexagParams))

            this.setState({
                loadingState: {
                    all: 32,
                    loaded: 28,
                }
            })
            let godex = await this.promiseHandler(() => this.api.godex.get('rate', {
                to: destination.token.symbol,
                from: deposit.token.symbol,
                amount: 1,
            }))

            this.setState({
                loadingState: {
                    all: 32,
                    loaded: 30,
                }
            })

            let simpleSwap = await this.promiseHandler(() => this.api.simpleSwap.get('exchange', {
                query: {
                    fixed: SIMPLE_SWAP_FIXED,
                    currency_from: deposit.token.symbol.toLowerCase(),
                    currency_to: destination.token.symbol.toLowerCase()
                }
            }))


            let response = {
                '1inch': oneInch,
                'paraswap': paraswap,
                'dexag': dexag,
                'godex': godex,
                'coinswitch': coinSwitch,
                'simpleSwap': simpleSwap,
            }

            let result = this.getSortedResult(response);

            if(result.length > 0) {
                if(deposit.value) {
                    pair.destination.value = (deposit.value * result[0].rate).toFixed(6);
                } else if(destination.value) {
                    pair.deposit.value = (destination.value / result[0].rate).toFixed(6);
                }
            } else {
                toast.error('This Pair is not available.')
            }


            this.setState({
                pair,
                rates: result,
                rate: result.length > 0 ? result[0] : undefined,
                loading: false,
                showMore: false,
                hasEnoughBalance: !this.state.max || Number(pair.deposit.value) <= Number(this.state.max.toSignificant(6))
            })

        } else {
            this.setState({
                pair,
                showMore: false
            })
        }
    }

    onSelect = async (token, type) => {
        let pair = this.state.pair;

        pair[type].token = token;
        this.setState({
            pair
        }, this.fetchPrices.bind(this, this.state.pair))
    }

    onSwapTokens = () => {
        let pair;
        this.setState(prevState => {
            pair = {
                deposit: {
                    ...prevState.pair.destination,
                },
                destination: {
                    ...prevState.pair.deposit,
                }
            }
            return {
                pair
            }
        }, () => this.fetchPrices(pair));
    }

    onUserInputHandler = (value, type, max) => {
        this.setState(prevState => {
            const pair = prevState.pair;
            pair[type].value = value;
            if(prevState.rate) {
                if(type === 'deposit') {
                    pair.destination.value = (value * prevState.rate.rate).toFixed(6);
                } else if(type === 'destination') {
                    pair.deposit.value = (value / prevState.rate.rate).toFixed(6);
                }
            }
            return {
                pair,
                hasEnoughBalance: !max || Number(value) <= Number(max.toSignificant(6))
            }
        })
    }

    selectRate = (rate) => {
        this.setState(prevState => {
            let pair = {
                ...prevState.pair
            };
            if(pair.deposit.value) {
                pair.destination.value = (pair.deposit.value * rate.rate).toFixed(6);
            } else if(pair.destination.value) {
                pair.deposit.value = (pair.destination.value / rate.rate).toFixed(6);
            }

            return {
                pair,
                rate
            }
        })
    }

    setBuyState = (state = 'not_started') => {
        this.setState({
            buyState: state,
        })
    }

    setDefaultBuyState = () => {
        setTimeout(this.setBuyState, 4000)
    }

    oneInchBuyHandler = async (pair, rate) => {
        try {
            this.setBuyState('initializing')
            let { deposit, destination } = pair;
            let allowance;
            let token = deposit.token.symbol === 'ETH' ? ETHER : new Token(deposit.token.chainId, deposit.token.address, deposit.token.decimals, deposit.token.symbol, deposit.token.name);

            const spenderRes = await this.api.oneInch.get('spender');
            const spender = spenderRes.data.address;

            let fromAmount = deposit.value * (10 ** deposit.token.decimals);
            const amount = new TokenAmount(token, `${fromAmount}`);

            if(deposit.token.symbol !== 'ETH') {
                this.setBuyState('allowance')

                let contract = getContract(deposit.token.address, ERC20_ABI, this.props.web3.library, this.props.web3.account);

                allowance = await contract.functions.allowance(this.props.web3.account, spender);
                allowance = new TokenAmount(token, allowance.toString())

                if(!amount.lessThan(allowance)) {
                    this.setBuyState('approving')

                    let approve = await contract.functions.approve(spender, amount.raw.toString());

                    if(approve) {
                        window.open(`https://etherscan.io/tx/${approve}`, '_blank')
                    }
                }
            }

            if(deposit.token.symbol === 'ETH' || amount.lessThan(allowance)) {
                this.setBuyState('create_tx')

                const res = await this.api.oneInch.get('swap', {
                    fromTokenAddress: deposit.token.address,
                    toTokenAddress: destination.token.address,
                    amount: fromAmount,
                    fromAddress: this.props.web3.account,
                    slippage: this.props.slippage,
                    protocols: rate.platform,
                })
                const tx = res.data.tx;

                this.setBuyState('send_tx')

                this.web3.eth.sendTransaction(tx, async (err, transactionHash) => {
                    if (err) {
                        this.setBuyState('failed')

                        this.setDefaultBuyState()

                        if(err.code === 4001){
                            toast.error('You canceled exchange process')
                        } else {
                            toast.error('An Error was occurred')
                        }
                        this.isExchangeInProgress = false;
                        return false;
                    }

                    this.setBuyState('submitted')

                    this.setDefaultBuyState()
                    this.isExchangeInProgress = false;
                    toast.success('Your Exchange order submitted successfully');
                    window.open(`https://etherscan.io/tx/${transactionHash}`, '_blank');
                });
            } else {
                toast.error('Token approval is pending or rejected');
            }
            this.isExchangeInProgress = false;

        } catch(e) {
            this.setBuyState('failed')

            this.setDefaultBuyState()

            this.isExchangeInProgress = false;

            if(e.hasOwnProperty('code')) {
                if(e.code === 4001) {
                    toast.error('You canceled exchange process')
                } else {
                    toast.error('An Error was occurred')
                }
            } else {
                if(e.hasOwnProperty('response')) {
                    if(e.response.status === 500) {
                        toast.error('This pair is not available on this platform, Please select another one.')
                    } else {
                        toast.error('An Error was occurred')
                    }
                } else {
                    toast.error('An Error was occurred')
                }
            }
        }
    }

    paraSwapBuyHandler = async (pair, rate) => {
        try {
            this.setBuyState('initializing')

            let { deposit, destination } = pair;
            let canExchange = false;
            const paraswap = this.api.paraswap.setWeb3Provider(window.ethereum);

            let fromAmount = (deposit.value * (10 ** deposit.token.decimals)).toFixed(0);
            let toAmount = (destination.value * (10 ** destination.token.decimals)).toFixed(0);

            this.setBuyState('allowance')
            const allowanceRes = await paraswap.getAllowance(this.props.web3.account, deposit.token.address);
            if(Number(allowanceRes.allowance) < fromAmount) {
                this.setBuyState('approving')
                const approve = await paraswap.approveToken(fromAmount, this.props.web3.account, deposit.token.address);

                if(approve) {
                    window.open(`https://etherscan.io/tx/${approve}`, '_blank')
                }
            } else {
                canExchange = true;
            }

            if(canExchange) {
                this.setBuyState('create_tx')
                const rates = await paraswap.getRate(deposit.token.address, destination.token.address, fromAmount);
                const selectedRoute = rates.others.find(item => item.exchange === rate.platform);
                const txRoute = [
                    {
                        ...rates.bestRoute[0],
                        ...selectedRoute
                    }
                ]
                rates.bestRoute = txRoute;
                const txParams = await paraswap.buildTx(deposit.token.address, destination.token.address, fromAmount, toAmount, rates, this.props.web3.account, process.env.REACT_APP_NAME, '');


                this.setBuyState('send_tx')
                this.web3.eth.sendTransaction(txParams, async (err, transactionHash) => {
                    if (err) {
                        this.setBuyState('failed')

                        this.setDefaultBuyState()

                        if(err.code === 4001){
                            toast.error('You canceled exchange process')
                        } else {
                            toast.error('An Error was occurred')
                        }

                        this.isExchangeInProgress = false;
                        return false;
                    }
                    this.setBuyState('submitted')

                    this.setDefaultBuyState()
                    this.isExchangeInProgress = false;
                    toast.success('Your Exchange order submitted successfully');
                    window.open(`https://etherscan.io/tx/${transactionHash}`, '_blank');
                });
            } else {
                this.setBuyState('pending')

                this.setDefaultBuyState()
            }
        } catch(e) {
            this.setBuyState('failed')

            this.setDefaultBuyState()

            if(e.hasOwnProperty('code')) {
                if(e.code === 4001) {
                    toast.error('You canceled exchange process')
                } else {
                    toast.error('An Error was occurred')
                }
            } else {
                toast.error('An Error was occurred')
            }
            this.isExchangeInProgress = false;
        }
    }

    dexagBuyHandler = async (pair, rate) => {
        let valid = false;
        try {
            this.setBuyState('initializing')

            let { deposit, destination } = pair;

            const sdk = this.api.dexag.sdk;
            sdk.registerStatusHandler((status, data) => {
                switch(status) {
                    case 'rejected': {
                        this.setBuyState('failed')
                        this.setDefaultBuyState()
                        toast.error('You canceled exchange process');
                        break;
                    }
                    case 'network': {
                        toast.error('Please change your network to mainnet to continue')
                        break;
                    }
                    case 'send_trade': {
                        this.setBuyState('submitted')
                        this.setDefaultBuyState()
                        window.open(`https://etherscan.io/tx/${data}`, '_blank');
                        break;
                    }
                    case 'failed': {
                        this.setBuyState('failed')
                        this.setDefaultBuyState()
                        toast.error('Your transaction was failed');
                        window.open(`https://etherscan.io/tx/${data}`, '_blank');
                        break;
                    }
                }
            });

            this.setBuyState('create_tx')
            const tx = await sdk.getTrade({to: destination.token.symbol, from: deposit.token.symbol, fromAmount: deposit.value, dex: rate.platform});


            this.setBuyState('validation')
            valid = await sdk.validate(tx);

            if (valid) {
                sdk.trade(tx);
            }

        } catch(e) {
            this.setBuyState('failed')
            this.setDefaultBuyState()

            if(e.hasOwnProperty('code')) {
                if(e.code !== 4001) {
                    toast.error('An Error was occurred')
                }

                if(!valid) {
                    toast.error('Transaction failed please try again later or select another platform')
                }
            } else {

                if(!valid) {
                    toast.error('Transaction failed please try again later or select another platform')
                }
                toast.error('An Error was occurred')
            }

            this.isExchangeInProgress = false;
        }

    }

    coinSwitchBuyHandler = async (pair, rate) => {
        try {
            this.setBuyState('initializing')
            let { deposit, destination } = pair;


            this.setBuyState('validation')
            if(deposit.value < rate.min || deposit.value >= rate.max) {
                toast.error(deposit.value < rate.min ? `The minimum value for this transaction is ${rate.min}` : `The maximum value for this transaction is ${rate.max}`)
                this.setBuyState('failed')
                this.setDefaultBuyState()
                return;
            }

            this.setBuyState('create_tx')
            const res = await this.api.coinSwitch.get('order', {
                depositCoin: deposit.token.symbol.toLowerCase(),
                destinationCoin: destination.token.symbol.toLowerCase(),
                depositCoinAmount: deposit.value,
                destinationAddress: {
                    address: this.props.web3.account,
                    tag: null
                },
            })

            if(res.data.success) {
                this.setBuyState('submitted')
                this.setState({
                    showQrModal: true,
                    coinSwitchOrder: {
                        ...res.data.data
                    },
                    simpleSwapOrder: {},
                })
                this.setDefaultBuyState()
            }

        } catch(e) {
            toast.error('An Error was occurred')
            this.setBuyState('failed')
            this.setDefaultBuyState()
        }
    }

    simpleSwapBuyHandler = async (pair, rate) => {
        try {
            this.setBuyState('initializing')
            let { deposit, destination } = pair;


            this.setBuyState('validation')
            if(deposit.value < rate.min || (rate.max > 0 && deposit.value >= rate.max)) {
                toast.error(deposit.value < rate.min ? `The minimum value for this transaction is ${rate.min}` : `The maximum value for this transaction is ${rate.max}`)
                this.setBuyState('failed')
                this.setDefaultBuyState()
                return;
            }

            this.setBuyState('create_tx')
            const res = await this.api.simpleSwap.set('exchange', {
                data: {
                    fixed: SIMPLE_SWAP_FIXED,
                    currency_from: deposit.token.symbol.toLowerCase(),
                    currency_to: destination.token.symbol.toLowerCase(),
                    amount: deposit.value,
                    address_to: this.props.web3.account,
                }
            })

            if(res) {
                if(res.code && res.code !== 200) {
                    toast.error(res.message);
                } else {
                    toast.success('Your order submitted successfully!')
                    this.setBuyState('submitted')
                    this.setState({
                        showQrModal: true,
                        simpleSwapOrder: res,
                        coinSwitchOrder: {}
                    })
                    this.setDefaultBuyState()
                }
            }

        } catch(e) {
            toast.error('An Error was occurred')
            this.setBuyState('failed')
            this.setDefaultBuyState()
        }
    }

    godexBuyHandler = (pair, rate) => {

    }

    buyHandler = async () => {
        if(!this.isExchangeInProgress) {
            this.isExchangeInProgress = true;

            const { pair, rate } = this.state;
            let result;

            switch(rate.source) {
                case '1inch': {
                    result = await this.oneInchBuyHandler(pair, rate);
                    break;
                }
                case 'paraswap': {
                    result = await this.paraSwapBuyHandler(pair, rate);
                    break;
                }
                case 'dexag': {
                    result = await this.dexagBuyHandler(pair, rate);
                    break;
                }
                case 'coinswitch': {
                    result = await this.coinSwitchBuyHandler(pair, rate);
                    break;
                }
                case 'simpleSwap': {
                    result = await this.simpleSwapBuyHandler(pair, rate);
                    break;
                }
                case 'godex': {
                    result = await this.godexBuyHandler(pair, rate);
                    break;
                }
            }

            this.isExchangeInProgress = false;
        }
    }
    toggleShowMore = () => {
        this.setState(prevState => {
            return {
                showMore: !prevState.showMore,
            }
        })
    }

    render() {
        const {pair, rate, hasEnoughBalance, rates, loading, loadingState, buyState} = this.state;

        return (
            <Page>
                <StyledRow>
                    <Col xs={12} md={{ offset: 3, span: 6}}>
                        <CustomCard>
                            <Row>
                                <Col xs={12} style={{ marginBottom: 20 }}>
                                    <SwapInputPanel
                                        value={pair.deposit.value}
                                        onUserInput={this.onUserInputHandler}
                                        label='You Pay'
                                        onSelect={this.onSelect}
                                        selected={pair.deposit.token}
                                        currencies={tokens}
                                        type={'deposit'}
                                        id={'deposit'}
                                        onChangeBalance={this.onChangeBalance}
                                    />
                                </Col>
                                <Col xs={12} className={'gutter-b'} style={{ marginBottom: !rate ? '40px' : '0'}}>
                                    <SwapInputPanel
                                        value={pair.destination.value}
                                        onUserInput={this.onUserInputHandler}
                                        label='You Receive (estimated)'
                                        onSelect={this.onSelect}
                                        selected={pair.destination.token}
                                        currencies={tokens}
                                        type={'destination'}
                                        id={'destination'}
                                    />
                                </Col>
                                {!!rate ? (
                                    <Col xs={12} className={'d-flex justify-content-between align-items-center gutter-b'} style={{ marginBottom: 60 }}>
                                        <RateText fontWeight={400} fontSize={isMobile ? 12 : 14}>
                                            Exchange Rate
                                        </RateText>
                                        <RateText fontWeight={isMobile ? 400 : 500} fontSize={isMobile ? 12 : 16}>
                                            {pair.deposit.token && pair.destination.token ? `1 ${pair.deposit.token.symbol || pair.deposit.token.code} = ${rate.rate.toFixed(4)} ${pair.destination.token.symbol || pair.destination.token.code}` : null}
                                        </RateText>
                                    </Col>
                                ) : ''}

                                <Col xs={12} className={'d-flex flex-column align-items-stretch align-items-md-center justify-content-center'}>
                                    {loading  ? (
                                        <div className={`d-flex flex-column align-items-stretch justify-content-center align-self-stretch`}>
                                            <RateText
                                                fontWeight={400}
                                                fontSize={isMobile ? 12 : 16}
                                            >
                                                We are searching in {loadingState.loaded} of {loadingState.all} exchanges
                                            </RateText>
                                            <ProgressContainer>
                                                <ProgressBar
                                                    now={(loadingState.loaded / loadingState.all * 100).toFixed(0)}
                                                    variant={loadingState.loaded === loadingState.all ? 'success' : 'primary'}
                                                    className={'align-self-stretch w-100 progress-xs'} />
                                            </ProgressContainer>

                                        </div>
                                    ) : (
                                        <SwapButton
                                                onClick={this.buyHandler}
                                                className={'py-3'}
                                                disabled={!(pair.deposit.token && pair.destination.token && pair.deposit.value && pair.destination.value && hasEnoughBalance && Number(pair.deposit.value) > 0 && rate) || rate.platform === 'godex'}
                                                variant={
                                                    pair.deposit.token && pair.destination.token && pair.deposit.value && pair.destination.value && hasEnoughBalance && Number(pair.deposit.value) > 0 && rate ? 'primary' : 'outline-primary'
                                                }
                                        >
                                            {
                                                !pair.deposit.token || !pair.destination.token ? 'Select a pair' :
                                                    !hasEnoughBalance ? `Insufficient ${pair.deposit.token.symbol} Balance` :
                                                        !pair.deposit.value || !pair.destination.value ? 'Please Enter Amount' :
                                                            !rate && !loading ? 'This pair is not available' :
                                                                rate.platform === 'godex' ? 'This pair is not available on this platform to exchange' :
                                                                    Number(pair.deposit.value) > 0 ? BUY_STATES[buyState] : 'Please Enter Amount'
                                            }
                                        </SwapButton>
                                    )}

                                </Col>
                                {rates.length > 0 && !loading && (
                                    <Col xs={12} className={'d-flex flex-column'} style={{ marginTop: 36 }}>
                                        {rates.map((item, index) => {
                                            return (
                                                <PlatformCard key={`show-all-${item.platform}`} onClick={this.selectRate.bind(this, item)} className={`d-flex align-items-center justify-content-between`}>
                                                    <div className={'d-flex align-items-center'} style={{ flex: 1}}>
                                                        <Logo src={toAbsoluteUrl(`/media/dex/${DEXesImages[item.platform]}`)} size={30} alt={item.platform}/>
                                                        <RateText fontSize={isMobile ? 12 : 16} fontWeight={isMobile ? 400 : 500} style={{ flex: 1}}>
                                                            {DEXesName[item.platform]}
                                                        </RateText>
                                                    </div>

                                                    <div className={'d-flex flex-row align-items-center justify-content-center'} style={{ flex: 1}}>
                                                        <RateText fontWeight={500} fontSize={isMobile ? 12 : 16} className={isMobile ? 'mr-2' : 'mr-3'}>
                                                            {item.rate.toFixed(6)} {pair.destination.token.symbol}/{pair.deposit.token.symbol}
                                                        </RateText>
                                                        {index === 0 ? (
                                                            <span className="label label-inline label-lg label-light-primary">Best</span>
                                                        ) : (
                                                            <span style={{ display: 'block', flexBasis: 55, minWidth: 55}}/>
                                                        )}
                                                    </div>
                                                </PlatformCard>
                                            )
                                        })}
                                    </Col>
                                )}
                            </Row>
                        </CustomCard>
                    </Col>
                </StyledRow>
                <Modal
                    show={this.state.showQrModal}
                    centered
                    onHide={() => {
                        this.setState({
                            showQrModal: false,
                        })
                    }}
                    size={'lg'}
                >
                    <Modal.Body className={'d-flex flex-column'} style={{ minHeight: 480, padding: '30px 60px' }}>
                        <RowBetween style={{ padding: '8px 0 24px' }}>
                            <div />
                            <CloseIcon onClick={() => {
                                this.setState({
                                    showQrModal: false,
                                })
                            }} />
                        </RowBetween>
                        <div className="d-flex flex-column flex-grow-1 justify-content-start align-items-center">
                            <h4 className="font-weight-bold text-white mt-0 mb-0" style={{ paddingTop: 30 }}>Order Submitted</h4>
                            {this.state.simpleSwapOrder.hasOwnProperty('id') ? (
                                <>
                                    <div style={{ paddingTop: 40, paddingBottom: 45, marginBottom: 'auto' }}>
                                        <QRCode value={this.state.simpleSwapOrder?.address_from || ''} fgColor={'#fff'} bgColor={'transparent'}/>
                                    </div>
                                    <div className="d-flex flex-column align-items-stretch align-self-stretch mb-4">
                                        <span className="text-white font-size-large mb-2 d-block">Send {this.state.simpleSwapOrder?.amount_from} {this.state.simpleSwapOrder?.currency_from?.toUpperCase()} to the address below</span>
                                        <span className="text-white-50 font-size-sm">Exchange will automatically initiate once deposit is detected on below address</span>
                                    </div>

                                    <div className={'align-self-stretch d-flex flex-column mb-3'}>
                                        <span className="text-white-50 mb-2">ADDRESS</span>
                                        <span className="text-white font-weight-bold">{this.state.simpleSwapOrder?.address_from}</span>
                                    </div>
                                    <div className={'align-self-stretch d-flex flex-column'}>
                                        <span className="text-white-50 mb-2">Order ID</span>
                                        <span className="text-white">{this.state.simpleSwapOrder?.id}</span>
                                    </div>
                                </>
                            ) : (
                                <>
                                    <div style={{ paddingTop: 40, paddingBottom: 45, marginBottom: 'auto' }}>
                                        <QRCode value={this.state.coinSwitchOrder?.exchangeAddress?.address || ''} fgColor={'#fff'} bgColor={'transparent'}/>
                                    </div>
                                    <div className="d-flex flex-column align-items-stretch align-self-stretch mb-4">
                                        <span className="text-white font-size-large mb-2 d-block">Send {this.state.coinSwitchOrder?.expectedDepositCoinAmount} {this.state.pair?.deposit?.token?.symbol || 'ETH'} to the address below</span>
                                        <span className="text-white-50 font-size-sm">Exchange will automatically initiate once deposit is detected on below address</span>
                                    </div>

                                    <div className={'align-self-stretch d-flex flex-column mb-3'}>
                                        <span className="text-white-50 mb-2">ADDRESS</span>
                                        <span className="text-white font-weight-bold">{this.state.coinSwitchOrder?.exchangeAddress?.address}</span>
                                    </div>
                                    <div className={'align-self-stretch d-flex flex-column'}>
                                        <span className="text-white-50 mb-2">Order ID</span>
                                        <span className="text-white">{this.state.coinSwitchOrder?.orderId}</span>
                                    </div>
                                </>
                            )}
                        </div>
                    </Modal.Body>
                </Modal>
            </Page>
        )
    }
}

const mapStateToProps = state => {
    return {
        slippage: state.user.userSlippageTolerance
    }
}

export default connect(mapStateToProps)(
    withWeb3Account(
        InstantSwap
    )
);
