import axios from 'axios';

export default class MarketApi {
    constructor() {
        this.baseURL = 'https://api.ipify.org/'
        this.instance = axios.create({
            baseURL: this.baseURL,
            timeout: 30000,
        })

        this.instance.interceptors.response.use((res) => {
            if(res.hasOwnProperty('data')) {
                return res.data.ip;
            } else {
                return res;
            }
        }, (err) => {
            return Promise.reject(err)
        })
    }

    get(type, payload = {}) {
        switch(type) {
            case 'ip': {
                return this.getUserApi(payload)
            }
            default: {
                return new Promise((resolve) => {
                    resolve(null);
                })
            }
        }
    }

    getUserApi(payload) {
        return this.instance.get(`?format=${payload.format || 'json'}`)
    }
}
